#main {
	transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
}
main .content {
	position: relative;
	margin: 0 auto;
	padding: calc(64px + 1em) 2em 1em;
	max-width: 960px;
	box-sizing: content-box;
	font-size: 20px;
	line-height: 30px;
}
@media(max-width: 800px){
	main .content {
		font-size: 18px;
		line-height: 20px;
		padding-left: 1em;
		padding-right: 1em;
	}
	main .content h1 {
		font-size: 1.4em;
	}
}
@media(max-width: 480px){
	main .content {
		font-size: 16px;
		line-height: 18px;
	}
	main .content h1 {
		font-size: 1.2em;
	}
}
@media(min-width: 960px){
	main .content {
		text-align: justify;
	}
	main .content .pull-right {
		float: right;
	}
}
main .content .loader,
	main .content .error {
	position: absolute;
	top: calc(45vh - 64px);
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
}
main .content h1,main .content h2,main .content h3,main .content h4,main .content h5,main .content h6 {
	line-height: initial;
}
main .content figure {
	text-align: center;
}
main .content figure img {
	margin: auto;
}
main .content figure figcaption {
	color: #999999;
	font-style: italic;
	margin: 1em auto 2em;
}
.md-theme-default main .content code:not(.hljs) {
	background: none;
}
.requirements {
	color: #999999;
	display: block;
	text-align: right;
	font-style: italic;
	font-size: 0.8em;
	line-height: 1em;
}



