.md-sidenav.md-left .md-title {
    -ms-flex: 1;
        flex: 1;
}
.md-sidenav.md-left:not(.modal) .md-sidenav-content {
    top: 64px;
}
.md-sidenav.md-left:not(.modal) .md-toolbar,
		.md-sidenav.md-left:not(.modal) .md-sidenav-backdrop {
    display: none;
}
.md-sidenav.md-left:not(.modal).md-active + main {
    margin-left: 304px;
}
.md-sidenav.md-left .md-list.md-theme-default {
    padding: 0;
}
.md-sidenav.md-left .md-list.md-theme-default .md-list-item-expand > .md-list-item-container {
    border-top: 1px solid #CCCCCC;
}
.md-sidenav.md-left .md-list.md-theme-default .md-list-item-expand.md-active > .md-list-item-container {
    font-weight: bold;
    background: hsl(231, 50%, 90%);
}
.md-sidenav.md-left .md-list.md-theme-default .md-list-item .router-link-active.md-list-item-container {
    font-weight: bold;
}



