#main .gallery {
	text-align: center;
}
#main .gallery ul {
	margin: auto;
	padding: 0;
}
#main .gallery li {
	list-style: none;
	display: inline-block;
	margin: 4px;
}
#main .gallery li > a {
	position: relative;
	overflow: hidden;
	display: inline-table;
}
#main .gallery li > a > p {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0;
	background-color: rgba(0,0,0,0.75);
	color: #999999;
	transform: translateY(+100%);
	transition: transform 0.4s;
}
#main .gallery li > a:hover > p {
	transform: translateY(0);
}



