.search-bar {

	height: 40px;
	position: relative;
	background-color: rgba(0,0,0,0.25);
	border-radius: 20px;
}
.search-bar form {

	width: 320px;

	display: inline-block;

	overflow: hidden;
}
.search-bar .md-button.md-icon-button {

	margin: 0;
}
.search-bar input[type='search'] {

	color: hsl(231, 0%, 90%);

	border: none;

	background: transparent;

	outline: none;

	-webkit-appearance: none;

	   -moz-appearance: none;

	        appearance: none;

	height: 40px;

	padding: 10px 8px 8px 0;

	width: 100%;

	font-size: 16px;
}
.search-bar.focus {

	background-color: white;

	color: #757575;
}
.search-bar.focus input[type='search'] {

	color: #757575;
}
.search-bar.focus .suggestions.md-list {

	display: block;
}
@media(max-width: 800px) {
	.search-bar {

		background: transparent;
	}
	.search-bar form {

		width: 0;
	}
	.search-bar.focus {

		position: absolute;

		left: 10px;

		right: 10px;
	}
	.search-bar.focus form {

		width: calc(100% - 60px);
	}
}
.search-bar .suggestions.md-list {

	display: none;

	position: absolute;

	top: 100%;

	left: 30px;

	right: 30px;

	background: #f0f0f0;

	box-shadow: 0 8px 10px -5px rgba(0, 0, 0, 0.2), 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12);
}
.search-bar .suggestions.md-list .md-list-item a:focus {

	background: #ffffff;

	box-shadow: inset 0 0 2px #FFC107;
}
.search-bar .suggestions.md-list .no-results {

	height: 48px;

	line-height: 48px;

	color: #757575;

	background: #f0f0f0;

	text-align: center;

	font-style: italic;
}



