@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(/static/fonts/MaterialIcons-Regular.eot); /* For IE6-8 */
  src: local('Material Icons'),
       local('MaterialIcons-Regular'),
       url(/static/fonts/MaterialIcons-Regular.woff2) format('woff2'),
       url(/static/fonts/MaterialIcons-Regular.woff) format('woff'),
       url(/static/fonts/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

.fadein-enter-active {
	transition: opacity 1s;
	opacity: 1;
}

.fadein-enter, .fadein-leave-active {
	opacity: 0;
}

.error {
	color: #f00000;
	font: 1.5rem bold Roboto,Noto Sans,Noto,sans-serif;
}

.md-icon-button:focus {
	box-shadow: inset 0 0 2px 2px #FFC107;
}



